.navigation {
  top: 0;
  left: -100%;
  position: absolute;
  z-index: 2;
  min-width: 100vw;
  min-height: 100vh;
  transition: 0.5s all ease-out;
  background-color: #111518;
  &__list {
    margin-top: 80px;
    margin-left: 28px;
    margin-bottom: 15px;
  }
  &__item {
    margin-bottom: 15px;
    &:nth-child(4) {
      margin-bottom: 38px;
    }
  }
  &__link {
    text-decoration: none;
    color: #fff;
    font-size: 22px;
    font-weight: 500;
    line-height: 26px;
    transition: 0.2s all;
    &:hover {
      color: #0ec261;
      transition: 0.2s all;
    }
  }
  &__social {
    min-height: 40px;
    display: flex;
  }
  &__icon {
    display: block;
    min-width: 32px;
    min-height: 32px;
    background-repeat: no-repeat;
    margin-right: 24px;
  }
  &__icon--telegram {
    background-image: url("../../../icons/Telegram_white.svg");
  }
  &__icon--instagram {
    background-image: url("../../../icons//Instagram_white.svg");
  }
  &__icon--facebook {
    background-image: url("../../../icons//Facebook_white.svg");
  }
  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 100vh;
  }
  &.active {
    transition: 0.5s all ease;
    display: block;
    left: 0;
  }
}

@media (min-width: 768px) {
  .navigation {
    &__list {
      margin-left: 97px;
      margin-right: 97px;
    }
    &__wrapper {
      display: flex;
      justify-content: center;
      min-height: 100vh;
    }
    &__link {
      font-size: 28px;
      line-height: 26px;
    }
    &__item {
      margin-bottom: 25px;
      &:nth-child(4) {
        margin-bottom: 38px;
      }
    }
  }
}

@media(min-width: 1024px){
  .navigation{
    background: linear-gradient(to right, rgba(17, 21, 24, 1) 66%, rgba(17, 21, 24, 0.5));
  }
}