.burger__menu--header{
    width: 21px;
    height: 16px;
    span{
        border-radius: 1px;
        background-color: #121212;
    }
    @media(min-width:768px){
        display: none;
    }
}

.burger__menu--aside{
    width: 33px;
    height: 18px;
    display: none;
    span{
        background-color: #FFF;
    }
    @media(min-width:768px){
        display: block;
    }
    @media(min-width: 1024px){
        width: 24px;
    }
}

.burger__menu{
    margin-left: 5px;
    z-index: 5;
    position: relative;
    cursor: pointer;
    transition: .2s all;
    span{
        transition: .2s all;
        position: absolute;
        width: 100%;
        height: 3px;
        left: 0;
        border-radius: 2px;
    }
    span:nth-child(2){
        top: 45%;
    }
    span:nth-child(3){
        top: 90%
    }
    &.active{
        span{
            transition: .2s all;
            background-color: #FFF;
        }
        span:nth-child(1){
            top:50%;
            transform: rotate(45deg);
        }
        span:nth-child(2){
            opacity: 0;
        }
        span:nth-child(3){
            top: 50%;
            transform: rotate(-45deg);
        }
    }
}
